.page-home {
  display: flex;
  flex-direction: column;
}
.page-home .header {
  height: 44px;
  padding: 10px 20px;
  background-color: #d43d3d;
  display: flex;
  justify-content: space-between;
}
.page-home .header .hd-l {
  width: 40px;
  background: url(data:image/png;base64,) no-repeat center;
  background-size: contain;
}
.page-home .header .hd-m {
  width: 100px;
  background: url(data:image/png;base64,) no-repeat center;
  background-size: cover;
}
.page-home .header .hd-r {
  width: 40px;
  background: url(data:image/png;base64,) no-repeat center;
  background-size: contain;
}
.page-home .navbar {
  height: 37px;
  background-color: #f4f5f6;
  border-bottom: 1px solid rgba(221, 221, 221, 0.4);
  display: flex;
}
.page-home .navbar .navlist {
  flex: 1 1;
  line-height: 36px;
  overflow: auto;
  overflow-y: hidden;
  white-space: nowrap;
}
.page-home .navbar .navlist .nav {
  display: inline-block;
  padding: 0 14px;
  font-size: 17px;
  color: #505050;
}
.page-home .navbar .navlist .nav.active {
  color: #f85959;
}
.page-home .navbar .plusnav {
  position: relative;
  width: 40px;
  box-shadow: -4px 0 5px -3px #aaa;
}
.page-home .navbar .plusnav::before,
.page-home .navbar .plusnav::after {
  content: " ";
  display: block;
  position: absolute;
  background-color: #f85959;
}
.page-home .navbar .plusnav::before {
  width: 18px;
  height: 2px;
  left: 11px;
  top: 17px;
}
.page-home .navbar .plusnav::after {
  width: 2px;
  height: 18px;
  left: 19px;
  top: 9px;
}
.page-home .feedlist {
  flex: 1 1;
  overflow: hidden;
  overflow-y: auto;
}
.page-home .feedlist .loading {
  font-size: 14px;
  width: 90px;
  margin: 0 auto;
  padding: 2px 0 2px 20px;
  background: url(data:image/gif;base64,) no-repeat left center;
  background-size: 16px;
  color: #062b4e;
}
.page-home .feedlist .success {
  text-align: center;
  font-size: 14px;
  color: #5ae643;
  padding-top: 4px;
  -webkit-animation: successShowAndHide 2s linear forwards;
          animation: successShowAndHide 2s linear forwards;
}
.page-home .feedlist .feed-none {
  text-align: center;
  font-size: 12px;
  color: #999;
  padding: 10px 0;
}
@-webkit-keyframes successShowAndHide {
  0% {
    opacity: 0;
  }
  20% {
    opacity: 1;
  }
  80% {
    opacity: 1;
  }
  100% {
    opacity: 0.6;
  }
}
@keyframes successShowAndHide {
  0% {
    opacity: 0;
  }
  20% {
    opacity: 1;
  }
  80% {
    opacity: 1;
  }
  100% {
    opacity: 0.6;
  }
}
