@-webkit-keyframes alert-show {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes alert-show {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@-webkit-keyframes alert-hide {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
@keyframes alert-hide {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
.alert-root .alert-mask {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  overflow: hidden;
  display: none;
}
.alert-root .alert-mask.show {
  display: block;
  -webkit-animation: alert-show 200ms linear;
          animation: alert-show 200ms linear;
}
.alert-root .alert-mask.hideing {
  -webkit-animation: alert-hide 200ms linear forwards;
          animation: alert-hide 200ms linear forwards;
}
.alert-root .alert-mask.hide {
  display: none;
}
.alert-root .alert-mask .alert-site {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  overflow: hidden;
  overflow-y: auto;
}
.alert-root .alert-mask .alert {
  display: flex;
  flex-direction: column;
  width: 280px;
  border-radius: 4px;
  background-color: #fff;
  overflow: hidden;
}
.alert-root .alert-mask .alert .alert-content {
  flex: 1 1;
  text-align: center;
  padding: 30px 20px;
  font-size: 16px;
  max-height: 200px;
  word-break: break-all;
  overflow-y: auto;
}
.alert-root .alert-mask .alert .alert-btns {
  text-align: center;
  height: 54px;
  line-height: 54px;
  font-size: 14px;
  font-weight: bold;
  color: #062b4e;
  border-top: 1px solid #eee;
  display: flex;
  justify-content: space-between;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.alert-root .alert-mask .alert .alert-btns span {
  flex: 1 1;
}
.alert-root .alert-mask .alert .alert-btns span:active {
  background-color: #efefef;
  opacity: 0.6;
}
.alert-root .alert-mask .alert .alert-btns .btn-cancel {
  color: #408ad0;
  border-right: 1px solid #eee;
}
@-webkit-keyframes modal-from-bottom {
  from {
    top: 100%;
    opacity: 0;
  }
  to {
    top: 0;
    opacity: 1;
  }
}
@keyframes modal-from-bottom {
  from {
    top: 100%;
    opacity: 0;
  }
  to {
    top: 0;
    opacity: 1;
  }
}
@-webkit-keyframes modal-from-right {
  from {
    left: 100%;
    opacity: 0;
  }
  to {
    left: 0;
    opacity: 1;
  }
}
@keyframes modal-from-right {
  from {
    left: 100%;
    opacity: 0;
  }
  to {
    left: 0;
    opacity: 1;
  }
}
@-webkit-keyframes modal-from-left {
  from {
    left: -100%;
    opacity: 0;
  }
  to {
    left: 0;
    opacity: 1;
  }
}
@keyframes modal-from-left {
  from {
    left: -100%;
    opacity: 0;
  }
  to {
    left: 0;
    opacity: 1;
  }
}
.modal-root .modal-mask {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  opacity: 1;
  overflow: hidden;
}
.modal-root .modal-mask.from-bottom {
  -webkit-animation: modal-from-bottom 300ms ease-out;
          animation: modal-from-bottom 300ms ease-out;
}
.modal-root .modal-mask.from-right {
  -webkit-animation: modal-from-right 300ms ease-out;
          animation: modal-from-right 300ms ease-out;
}
.modal-root .modal-mask.from-left {
  -webkit-animation: modal-from-left 300ms ease-out;
          animation: modal-from-left 300ms ease-out;
}
.modal-root .modal-mask .modal-site {
  height: 100%;
  width: 100%;
  overflow: hidden;
  overflow-y: auto;
}
