.feed {
  display: block;
  margin: 0 10px;
  padding: 14px 0;
  border-bottom: 1px solid rgba(221, 221, 221, 0.4);
}
.feed:last-of-type {
  border-bottom-color: transparent;
}
.feed .feed-hd {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 36px;
  margin-bottom: 12px;
}
.feed .feed-hd .author-avatar {
  position: relative;
  width: 36px;
  margin-right: 10px;
}
.feed .feed-hd .author-avatar img {
  float: left;
  height: 100%;
  width: 100%;
  border-radius: 50%;
}
.feed .feed-hd .author-avatar .vip {
  position: absolute;
  right: 0;
  bottom: 0;
  z-index: 2;
  height: 14px;
  width: 14px;
  background: url(data:image/png;base64,) no-repeat center;
  background-size: 14px;
}
.feed .feed-hd .author-info {
  flex: 1 1;
}
.feed .feed-hd .author-info .name {
  color: #222;
  font-weight: bold;
  font-size: 14px;
}
.feed .feed-hd .author-info .label {
  font-size: 12px;
  color: #999;
}
.feed .feed-hd .author-friend {
  width: 60px;
  text-align: right;
  font-size: 14px;
}
.feed .feed-hd .author-friend .friend {
  color: #999;
}
.feed .feed-hd .author-friend .unfriend {
  color: #f85959;
}
.feed .feed-tit {
  font-size: 17px;
  line-height: 1.4;
  color: #111;
}
.feed .feed-tit .lighting {
  color: #f85959;
  font-weight: bold;
}
.feed:visited .feed-tit {
  color: #888;
}
.feed .feed-pic {
  overflow: hidden;
}
.feed .feed-info {
  display: flex;
  justify-content: space-between;
  margin-top: 6px;
  font-size: 12px;
  line-height: 20px;
  color: #999;
}
.feed .feed-info .info-l span {
  margin-right: 6px;
}
.feed .feed-info .info-l span.ad {
  background-color: #eee;
  padding: 0 2PX;
  color: #888;
  border-radius: 1px;
}
.feed .feed-info .info-l span.red {
  color: #f85959;
}
.feed .feed-info .info-r {
  display: flex;
  align-self: center;
}
.feed .feed-info .info-r .close {
  height: 14px;
  width: 20px;
  background: url(data:image/png;base64,) no-repeat center #efefef;
  background-size: 8px;
  border-radius: 2px;
}
.feed img {
  border-radius: 2px;
  width: 100%;
  float: left;
}
.feed.feed-smallpic .feed-l {
  display: inline-block;
  vertical-align: middle;
  width: 67%;
  padding-right: 12px;
}
.feed.feed-smallpic .feed-r {
  display: inline-block;
  vertical-align: middle;
  width: 33%;
  overflow: hidden;
}
.feed.feed-threepic .feed-pic {
  margin-top: 4px;
}
.feed.feed-threepic .feed-pic img {
  width: 33%;
}
.feed.feed-threepic .feed-pic img:nth-of-type(2) {
  margin: 0 0.5%;
}
.feed.feed-bigpic .feed-pic {
  margin-top: 4px;
}
.feed.feed-feedvideo .feed-pic {
  position: relative;
}
.feed.feed-feedvideo .feed-pic .feed-tit {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 4;
  width: 100%;
  color: #eee;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0));
  height: 40%;
  font-size: 16px;
  padding: 5px 10px;
  border-radius: 2px 2px;
}
.feed.feed-feedvideo .feed-pic .videoplay {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  height: 100%;
  width: 100%;
  background: url(data:image/png;base64,) no-repeat center;
  background-size: 50px;
}
.feed.feed-feedvideo .feed-pic .videotime {
  position: absolute;
  bottom: 6px;
  right: 6px;
  z-index: 3;
  line-height: 16px;
  padding: 0 8px;
  color: #eee;
  background-color: rgba(0, 0, 0, 0.4);
  font-size: 10px;
  border-radius: 8px;
}
